h1,
h2 {
  font-weight: normal;
}
li {
  display: inline-block;
}
a {
  display: block;
  padding: 12px 15px;
  border-bottom: none;
  color: #364149;
  background: 0 0;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  position: relative;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
code,
textarea {
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
}
code {
  padding: 0.2em;
  margin: 0 4px;
  font-size: 0.85em;
  line-height: 1.6em;
  border-radius: 4px;
  background-color: #f7f7f7;
}
textarea {
  width: 100%;
  height: 100px;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.37em 0.57em;
  margin: 0.57em;
  border: 1px solid #dbdbdb;
  border-radius: 0.28em;
  font-size: 1em;
  line-height: 1.4;
  outline: none;
  -webkit-transition: border-color 0.5s;
  -o-transition: border-color 0.5s;
  transition: border-color 0.5s;
  resize: none;
}
.xui-react-head {
  position: fixed;
  left: 0;
  top: 0;
  width: 300px;
  height: 100%;
  -webkit-transition: 0.3s linear;
  -o-transition: 0.3s linear;
  transition: 0.3s linear;
}
.xui-react-head.folded {
  -webkit-transform: translateX(-300px);
      -ms-transform: translateX(-300px);
          transform: translateX(-300px);
}
ul.nav {
  position: fixed;
  left: 0;
  top: 0;
  width: 300px;
  height: 100%;
  margin: 0;
  list-style-type: none;
  padding: 0;
  font-size: 14px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background: #fafafa;
  overflow-y: auto;
  overflow-x: hidden;
}
ul.nav li {
  width: 100%;
}
ul.nav > li:nth-child(2) {
  padding: 6px;
  border-bottom: 1px #ccc solid;
}
ul.nav li ul {
  margin-left: 2em;
  padding-left: 0;
}
ul.nav li a.highlight {
  color: #008cff;
}
.log {
  font-size: 15px;
}
.log * {
  margin-bottom: 4px;
}
.xui-react-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: end;
  overflow: hidden;
}
.xui-react-body .main {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 40px;
  margin-left: 320px;
  -webkit-transition: 0.3s linear;
  -o-transition: 0.3s linear;
  transition: 0.3s linear;
}
.xui-react-body .main.folded {
  margin-left: 0;
}
