blockquote {
  margin: 0;
  margin-bottom: 0.85em;
  padding: 0 15px;
  color: #858585;
  border-left: 4px solid #e5e5e5;
}
.markdown-section h1 {
  font-size: 2em;
}
.markdown-section h2 {
  font-size: 1.75em;
}
.markdown-section h3 {
  font-size: 1.5em;
}
.markdown-section h4 {
  font-size: 1.25em;
}
.markdown-section h5 {
  font-size: 1em;
}
.markdown-section h6 {
  font-size: 1em;
  color: #777;
}
.markdown-section > ul,
.markdown-section > ol {
  padding: 0;
  margin: 0;
  margin-bottom: 0.85em;
  padding-left: 2em;
}
.markdown-section a {
  color: #4183c4;
  text-decoration: none;
  background: 0 0;
  display: initial;
  padding: 4px;
}
.markdown-section img {
  border: 0;
  max-width: 100%;
}
.markdown-section > ol li,
.markdown-section > ul li,
.main > ul li {
  display: list-item;
  margin: 4px 0;
}
.markdown-section a:active,
.markdown-section a:focus,
.markdown-section a:hover {
  outline: 0;
  text-decoration: underline;
}
@media screen and (max-width: 800px) {
  .xui-react-body .main {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    padding: 40px 20px;
  }
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track-piece {
  background-color: #ccc;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: #999;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: #ccc;
  border-radius: 6px;
}



