.xui-react-switch {
  height: 20px;
  margin: 2px 4px;
  font-size: 15px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
}
.xui-react-switch input {
  display: none;
}
.xui-react-switch input:checked + label::after {
  content: attr(data-on);
  text-align: left;
  background: #4eb900;
  border-color: #4eb900;
  color: #fff;
}
.xui-react-switch input:checked + label::before {
  left: 22px;
}
.xui-react-switch input:checked + label.disabled::after {
  background: rgba(78, 185, 0, 0.5);
  border-color: rgba(78, 185, 0, 0.5);
}
.xui-react-switch label {
  position: relative;
  padding-left: 44px;
  height: 100%;
  display: inline-block;
  text-align: right;
  font-size: 14px;
  cursor: pointer;
}
.xui-react-switch label::before {
  content: '';
  position: absolute;
  left: 2px;
  top: 2px;
  width: 16px;
  height: 16px;
  display: inline-block;
  border-radius: 50%;
  background-color: #fff;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  z-index: 2;
}
.xui-react-switch label::after {
  content: attr(data-off);
  position: absolute;
  left: 0;
  top: 0;
  width: 40px;
  height: 100%;
  color: #8b8c90;
  font-size: 12px;
  padding: 0 2px;
  display: inline-block;
  text-align: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px #c6c8d1 solid;
  background-color: #f6f7fb;
  border-radius: 10px;
  z-index: 1;
  -webkit-box-shadow: inset 0 2px 5px 0 rgba(0, 10, 30, 0.1);
          box-shadow: inset 0 2px 5px 0 rgba(0, 10, 30, 0.1);
}
.xui-react-switch label.disabled {
  cursor: no-drop;
  color: #ccc;
}



