body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
}
.none {
  display: none;
}
.react-message {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  background: #000;
  padding: 10px 50px;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  font-size: 18px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-animation: 0.2s toToDown linear;
          animation: 0.2s toToDown linear;
  z-index: 99999;
}
pre {
  margin: 0;
}
.codes {
  color: #333333;
  overflow: auto;
  word-wrap: normal;
  margin: 0;
  padding: 0.85em 1em;
  margin-bottom: 0.275em;
  background: #f7f7f7;
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  width: 100%;
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.codes .codes_symbol {
  color: #f5871f;
}
.codes .codes_equal {
  color: #718c00;
}
.codes .codes_quot {
  color: #8959a8;
}
@-webkit-keyframes toToDown {
  0% {
    opacity: 0.2;
    -webkit-transform: translate3d(-50%, -100%, 0);
            transform: translate3d(-50%, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
  }
}
@keyframes toToDown {
  0% {
    opacity: 0.2;
    -webkit-transform: translate3d(-50%, -100%, 0);
            transform: translate3d(-50%, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(-50%, -50%, 0);
            transform: translate3d(-50%, -50%, 0);
  }
}



