.button_content .button_btn {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.37em 0.57em;
  margin: 0.57em;
  border: 1px solid #dbdbdb;
  border-radius: 0.28em;
  font-size: 1em;
  line-height: 1.4;
  padding-left: 1.14em;
  padding-right: 1.14em;
  white-space: nowrap;
  outline: none;
  background: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.button_content .button_btn:active {
  top: 1px;
}
.button_content .button_btn.button_default {
  color: #f8f9f8;
  border-color: #4eb900;
  background-color: #4eb900;
}
.button_content .button_btn.button_default:not(:disabled):hover {
  border-color: #3b8704;
  background-color: #3b8704;
}
.button_content .button_btn.button_danger {
  color: #fd7127;
  border-color: #fd7127;
  background-color: #fff;
}
.button_content .button_btn.button_danger:not(:disabled):hover {
  color: #f8f9f8;
  border-color: #fd7127;
  background-color: #fd7127;
}
.button_content .button_btn:disabled {
  color: #ccc;
  border-color: #ccc;
  background-color: #f8f9f8;
  cursor: not-allowed;
}



